import React from 'react';
import Menu1 from './Menu1';
import Menu2 from './Menu2';
import Menu3 from './Menu3';
import Topbar from './Topbar';

import '../css/layout.css';

class MainComp extends React.Component{

	constructor(props){
		super();
		
		this.state = {
			Menu:0,
		}
	}
	
	changeMenu = (menuIndex)=>{
		this.setState({menu:menuIndex});
		if(menuIndex == 0){
			this.props.history.push('/menu1');			
		}else if(menuIndex === 1){
			this.props.history.push('/menu2');		
		}else{
			this.props.history.push('/menu3');
		}
	}
	
	

	render(){
		return(
			<div>
			  <Topbar
                title="메인 메뉴"
            	/>

				<div className="wrap">
					<div className="menuBar">
						<ul className="tabs">
							<li className={`${this.state.menu === 0? 'active': ''}`} onClick={() => this.changeMenu(0)}>Sub Menu 1</li>
							<li className={`${this.state.menu === 1? 'active': ''}`} onClick={() => this.changeMenu(1)}>Sub Menu 2</li>
							<li className={`${this.state.menu === 2? 'active': ''}`} onClick={() => this.changeMenu(2)}>Sub Menu 3</li>
						</ul>
					</div>
				<div className="contentArea">
					{menuList[this.state.menu]}
				</div>
			</div>
		</div>
		);
	}
}

const menuList = {
  0: <Menu1 />,
  1: <Menu2 />,
  2: <Menu3 />,
};
	
export default MainComp;
